import {useState, useEffect} from 'react';
import recipeSvg from '../imgs/recipe.svg';
import {connect} from 'react-redux';
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Recipe from './Recipe';
import RecipeCard from './RecipeCard';
import langJson from '../json/language.json';
import recipesJson from '../json/recipes.json';

import './css/ResizeSvg.css';

function RecipeDb(props) {

    const generateRecipeCards = () => {
        const tempCards = [];
        tempCards.push(<div className="mt-4"></div>);
        tempCards.push(recipesJson.map((recipe, index) => <RecipeCard title={recipe.title} desc={recipe.desc} recipe={recipe.recipe} setCurrentRecipe={props.setCurrentRecipe} index={index+1} topImagesSize={topImagesSize}/>));
        return tempCards;
    }

    const [topImagesSize, setTopImagesSize] = useState('7x');

    useEffect(() => {
        if(props.windowSize >= 992) {
            setTopImagesSize('7x');
        } else {
            setTopImagesSize('5x');
        }
    }, [props.windowSize]);

    return (
        <div>
            <div className="container-fluid bg-info d-flex justify-content-between" style={{border: '2px solid black', borderStyle: 'solid none solid none'}}>
                <img className="resizeTopImages" src={recipeSvg}/>
                <div className="h1 container h-100" style={{transform: 'translateY(20%)'}}>{recipesJson[props.currentRecipe-1] ? recipesJson[props.currentRecipe-1].title : ''}</div>
                <button className="btn" onClick={() => props.currentRecipe ? props.setCurrentRecipe('') : props.setPageOpen('recipes')}><FontAwesomeIcon className="recipeFeatureBack" size={topImagesSize} icon={faTimes}/></button>
            </div>
            <div>
                {props.currentRecipe ?
                <Recipe setCurrentRecipe={props.setCurrentRecipe} recipe={recipesJson[props.currentRecipe-1]} overrideLang="en"/>
                :
                <div className="row mt-4 mx-3">{generateRecipeCards()}</div>
                }
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        lang: state.general.lang
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RecipeDb);